import React, { useState, useEffect } from "react";
import styled from "styled-components";
import html2canvas from "html2canvas"; // html2canvas import
import { useApiKey } from '../ApiKeyContext';

const Container = styled.div`
    z-index: 999;
    color: white;
    position: fixed;
    background-color: #262626;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: start;
    align-items: start;
    // border-radius: 8px;
    padding: 40px;
    width: 800px;
`;

const ChosenImg = styled.img`
    width: 320px;
    border-radius: 4%;
`;
const ImageWrapper = styled.div`

`;
const ButtonWrap = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 20px;
    right: 20px;
`;
const ImgDownloadBtn = styled.button`
    width: 40px;
    height: 40px;
    border-radius: 40px;
    border: 0px;
    background-color: #5F5F5F;
    right: 0;
    bottom: 0;
    cursor: pointer;
`;
const ButtonClose = styled.button`
    width: 40px;
    height: 40px;
    border-radius: 40px;
    border: 0px;
    background-color: #5F5F5F;
    margin-left: 12px;
    right: 0;
    bottom: 0;
    cursor: pointer;
`;
const TextWrapper = styled.div`
    margin-left: 40px;
`;
const CardText = styled.p`
    font-size: 16px;
    margin-top: 60px;
    margin-bottom: 0;
    color: #fff;
`;
const CardText2 = styled.p`
    font-size: 16px;
    margin-top: 12px;
    color:rgb(198, 198, 198);
`;

const WordName = styled.p`
    font-size: 28px;
    font-weight: bold;
    margin-top: 12px;
    margin-bottom: 20px;
`;
const CustomList = styled.ul`
    list-style: none;
    padding: 0;
    margin: 0;
`;
const ListItem = styled.li`
    position: relative;
    padding-left: 40px;
    margin-bottom: 12px;
    font-size: 20px;
    &:before {
        content: "${(props) => props.num}";
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        font-weight: bold;
        font-size: 14px;
        margin-right: 12px;
        background-color: #4B39EF;
        border-radius: 4px;
        width: 28px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
`;

function VocabularyImg({ word, imageUrl, meanings, sentence, synonymWords, closeOverlay }) {
    const [translatedSentence, setTranslatedSentence] = useState(""); //   
    const { apiKey } = useApiKey() || {};

    // GPT API   
    const translateSentence = async (text) => {
        try {
            const response = await fetch("https://api.openai.com/v1/chat/completions", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${apiKey}`,
                },
                body: JSON.stringify({
                    model: "gpt-3.5-turbo",
                    messages: [
                        {
                            role: "system",
                            content: "You are a professional translator from English to Korean. Your tranlate should have a only one sentence.: ",
                                        
                        },
                        { role: "user", content: text },
                    ],
                }),
            });

            const data = await response.json();
            const translation = data.choices[0].message.content.trim();
            console.log(translation)
            setTranslatedSentence(translation); //    
        } catch (error) {
            console.error(" :", error);
        }
    };

    // sentence    
    useEffect(() => {
        if (sentence) {
            translateSentence(sentence);
        }
    }, [sentence]);

    //    
    const handleDownload = () => {
        const container = document.getElementById("vocabulary-container");
        const buttonWrap = document.getElementById("Button"); //    
    
        if (buttonWrap) buttonWrap.style.display = "none"; //  
    
        html2canvas(container, {
            useCORS: true,
            allowTaint: false,
        }).then((canvas) => {
            if (buttonWrap) buttonWrap.style.display = ""; //   
    
            const link = document.createElement("a");
            link.href = canvas.toDataURL("image/png");
            link.download = word + ".png";
            link.click();
        });
    };
    

    return (
        <Container id="vocabulary-container">
            <ImageWrapper>
                {imageUrl ? (
                    <ChosenImg 
                        src={`data:image/png;base64,${imageUrl}`}               
                        alt="Selected vocabulary" />
                ) : (
                    <p>  .</p>
                )}
            </ImageWrapper>

            <TextWrapper>
                <WordName>{word}</WordName>
                <CustomList>
                {meanings.flat().map((meaning, index) => (
                    <ListItem key={index} num={index + 1}>
                        {meaning}
                    </ListItem>
                ))}
                </CustomList>
                <CardText>{sentence || " "}</CardText>
                <CardText2>{translatedSentence || " "}</CardText2>
            </TextWrapper>

            <ButtonWrap id="Button">
                <ImgDownloadBtn onClick={handleDownload}>
                    <img
                        src={process.env.PUBLIC_URL + '/download2.png'}
                        alt="Download icon"
                        style={{ width: "24px", height: "24px", borderRadius: "8px" }}
                    />
                </ImgDownloadBtn>
                <ButtonClose onClick={closeOverlay}>
                    <img
                        src={process.env.PUBLIC_URL + '/delete.png'}
                        alt="Delete Icon"
                        style={{ width: "24px", height: "24px", borderRadius: "8px" }}
                    />
                </ButtonClose>
            </ButtonWrap>
        </Container>
    );
}

export default VocabularyImg;
