import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import reportWebVitals from './reportWebVitals';
import Header from './component/Header';
import SearchAWord from './component/SearchAWord';
import GenerateImg from './component/GenerateImg';
import {BrowserRouter,Routes, Route} from 'react-router-dom'
import { ApiKeyProvider } from './ApiKeyContext';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <ApiKeyProvider>
        <Header />
      <Routes>
        <Route index element={<SearchAWord />} />
        <Route path="generate" element={<GenerateImg />} />
      </Routes>
      </ApiKeyProvider>

    </BrowserRouter>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
