import React, { useState } from "react";
import styled from "styled-components"
import { useNavigate } from "react-router-dom";
import Modal from "./Modal";
import { useApiKey } from "../ApiKeyContext";

const Container = styled.div`
    background-color: #000;
    width: 100%;
    box-sizing: border-box;
    padding: 32px 32px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    cursor: pointer;
`
const LogoWrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
`
const LogoImg = styled.img`
    height: 32px;
    cursor: pointer;
`
const LogoP = styled.p`
    color: rgba(255, 255, 255, 0.55);
    font-weight: 700;
    text-transform: uppercase;
    font-size: 20px;
    min-width: 200px;
`
const APIContainer = styled.div`
    color: rgba(255, 255, 255, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 999px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px 16px;
    transition: 0.3s ease;
    cursor: pointer;
    &:hover {
        color: #fff;
        border: 1px solid #fff;
    }
`

function Header(){
    const navigate = useNavigate();    

    const [isModalOpen, setModalOpen] = useState(true); //    
    const { setApiKey } = useApiKey(); // Context API     
  
    const handleAPIClick = () => {
      setModalOpen(true); //  
    };
  
    const handleCloseModal = () => {
      setModalOpen(false); //  
    };
  
    const handleSaveAPIKey = (key) => {
        setApiKey(key); // Context API   
        console.log('Updated API Key:', key);
        setModalOpen(false); //  
    };

    return(
    <Container>
        <LogoWrapper onClick={()=>navigate('/')}>
            <LogoImg src={process.env.PUBLIC_URL + '/vocapodLogo.png'}></LogoImg>
            <LogoP>Image Generator</LogoP>
        </LogoWrapper>
        <APIContainer onClick={handleAPIClick}>API Key</APIContainer>
        {isModalOpen && (
            <Modal
            isOpen={isModalOpen}
            onClose={handleCloseModal}
            onSave={handleSaveAPIKey}
            />
        )}
    </Container>
    )
}

export default Header;